'use client';

import Link from 'next/link';
import { Tool, categories } from '@/utils/tools';

interface ToolCardProps {
  tool: Tool;
}

export default function ToolCard({ tool }: ToolCardProps) {
  const category = categories.find(c => c.id === tool.category);

  return (
    <Link
      href={tool.path}
      className="group block p-6 bg-white dark:bg-gray-800 rounded-xl border border-gray-200 dark:border-gray-700 hover:border-blue-500 dark:hover:border-blue-400 hover:shadow-lg transition-all duration-200"
    >
      <div className="flex items-start gap-4">
        <div className={`p-3 rounded-lg ${category?.color || 'bg-gray-500'} bg-opacity-10`}>
          <span className="text-2xl">{getIcon(tool.icon)}</span>
        </div>
        <div className="flex-1">
          <h3 className="font-semibold text-gray-900 dark:text-white group-hover:text-blue-600 dark:group-hover:text-blue-400 transition-colors">
            {tool.name}
          </h3>
          <p className="mt-1 text-sm text-gray-600 dark:text-gray-400">
            {tool.description}
          </p>
          <span className="mt-2 inline-block text-xs px-2 py-1 rounded-full bg-gray-100 dark:bg-gray-700 text-gray-600 dark:text-gray-400">
            {category?.name}
          </span>
        </div>
      </div>
    </Link>
  );
}

function getIcon(iconName: string): string {
  const icons: Record<string, string> = {
    Braces: '{}',
    Key: 'üîë',
    Code: '</>',
    Link: 'üîó',
    Terminal: '>_',
    Clock: 'üïê',
    Search: 'üîç',
    GitCompare: '‚öñÔ∏è',
    Hash: '#Ô∏è‚É£',
    Fingerprint: 'üÜî',
    Lock: 'üîí',
    Palette: 'üé®',
    Layers: 'üìö',
    FileText: 'üìÑ',
    Type: 'Tt',
    List: '‚ò∞',
  };
  return icons[iconName] || 'üì¶';
}
